import React from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import {NavLink} from 'react-router-dom'

const Common = (props) => {
  return (
    <>
      <section id="header" className="home_div">
        <div className="container-fluid ">
          <div className="row">
            <div className="col-sm-10 mx-auto">
              <div className="row ">
                <div className="col-md-6 pt-5 pt-lg-0 my-5 det_home">
                  <h1>
                    {props.h1} <strong className="brand">{props.strong}</strong>
                  </h1>
                  <h2>{props.h2}</h2>
                  <div className="mt-5 ">
                    <NavLink to={props.path} className="btn_get_started">
                      {props.btn}
                    </NavLink>
                  </div>
                </div>

                <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img src={props.img} className="animated " id="anim" ></img>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};
export default Common;
