import React from 'react'
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import "../node_modules/bootstrap/dist/js/bootstrap.bundle"
import {NavLink} from 'react-router-dom'
import './index1.css'

const Navbar=()=>{
    return(
        <div className="row" >
        <div className="container-fluid col-sm-10">
        <nav className="navbar navbar-expand-lg navbar-light bg_nav">
  <a className="navbar-brand" href="#">First project</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto menu">
      <li className="nav-item menu_list">
        <NavLink exact activeClassName='nav_active' className="nav-link" to="/">Home</NavLink>
      </li>
      <li className="nav-item menu_list">
        <NavLink exact activeClassName='nav_active' className="nav-link" to="/Service">Service</NavLink>
      </li>
      <li className="nav-item menu_list">
        <NavLink exact activeClassName='nav_active' className="nav-link" to="/About">About</NavLink>
      </li>
      <li className="nav-item menu_list">
        <NavLink exact activeClassName='nav_active' className="nav-link" to="/Contact">Contact</NavLink>
      </li>
    </ul>
  </div>
</nav>
</div>
</div>
    )
}
export default Navbar